* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.skip-link {
  color: #fff;
  z-index: 100;
  background: #000;
  padding: 8px;
  position: absolute;
  top: -40px;
  left: 0;
}

.skip-link:focus {
  top: 0;
}

header {
  border-bottom: 1px solid #ddd;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

nav ul {
  list-style: none;
  display: flex;
}

nav li {
  margin-left: 20px;
}

a {
  color: #333;
  text-decoration: none;
}

main {
  min-height: calc(100vh - 140px);
  padding: 20px 0;
}

footer {
  text-align: center;
  border-top: 1px solid #ddd;
  padding: 20px 0;
}

.story-list {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.story-item {
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: transform .3s;
  overflow: hidden;
}

.story-item:hover {
  transform: translateY(-5px);
}

.story-img {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.story-content {
  padding: 15px;
}

.story-content h2 {
  margin-bottom: 5px;
}

.story-description {
  margin-bottom: 10px;
}

.story-date {
  color: #666;
  font-size: .9em;
}

.form-container {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  display: block;
}

input, textarea {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #333;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background: #555;
}

#map {
  height: 300px;
  margin-bottom: 15px;
}

.camera-container {
  margin-bottom: 15px;
}

#video {
  width: 100%;
  margin-bottom: 10px;
}

#canvas {
  display: none;
}

.camera-buttons {
  gap: 10px;
  display: flex;
}

.preview-container {
  margin: 15px 0;
}

#preview {
  max-width: 100%;
  max-height: 300px;
}

.alert {
  border-radius: 4px;
  margin-bottom: 15px;
  padding: 10px;
}

.alert-success {
  color: #155724;
  background-color: #d4edda;
}

.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slide-from-right {
  from {
    opacity: 0;
    transform: translateX(30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.page {
  animation: .3s forwards fade-in, .3s forwards slide-from-right;
}

.map-popup img {
  object-fit: cover;
  width: 100px;
  height: 100px;
  margin-bottom: 5px;
}

.update-banner {
  z-index: 1000;
  color: #fff;
  text-align: center;
  background: #007bff;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.update-banner button {
  color: #007bff;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 3px;
  margin: 0 5px;
  padding: 5px 10px;
}

.favorite-btn {
  cursor: pointer;
  background: #f8f9fa;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-left: 10px;
  padding: 5px 10px;
  font-size: .9em;
}

.favorite-btn:hover {
  background: #e9ecef;
}

.favorite-btn.is-favorite {
  color: #fff;
  background: #ff6b6b;
  border-color: #ff6b6b;
}

.story-actions {
  align-items: center;
  margin-top: 10px;
  display: flex;
}

#notification-section {
  background: #f8f9fa;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 15px;
}

#notification-section h3 {
  color: #333;
  margin-bottom: 10px;
}

#notification-status .alert {
  margin-bottom: 10px;
}

.alert-info {
  color: #0c5460;
  background-color: #d1ecf1;
  border: 1px solid #bee5eb;
}

.alert-warning {
  color: #856404;
  background-color: #fff3cd;
  border: 1px solid #ffeaa7;
}

.offline-indicator {
  color: #fff;
  z-index: 1000;
  background: #ff6b6b;
  border-radius: 5px;
  padding: 10px 20px;
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 10px #0000001a;
}

.install-prompt {
  color: #fff;
  z-index: 1000;
  text-align: center;
  background: #333;
  border-radius: 8px;
  padding: 15px 20px;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 15px #0003;
}

.install-prompt button {
  color: #fff;
  cursor: pointer;
  background: #007bff;
  border: none;
  border-radius: 4px;
  margin: 0 5px;
  padding: 8px 15px;
}

.install-prompt button:hover {
  background: #0056b3;
}

@media (width <= 768px) {
  .story-actions {
    flex-direction: column;
    align-items: stretch;
  }

  .favorite-btn {
    margin-top: 10px;
    margin-left: 0;
  }

  .install-prompt {
    left: 20px;
    right: 20px;
    transform: none;
  }
}

.loading-overlay {
  z-index: 1000;
  background: #fffc;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top-color: #333;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
